import React from 'react';
import SoundButton from './SoundButton';
import { View } from 'react-native';

const MinionDefaultSounds = (props) => {
  return (
    <View style={{flexDirection: 'row', justifyContent: 'space-around'}}>
      <SoundButton title='Play' cardID={props.cardID} sound='P' />
      <View style={{padding: 4}} />
      <SoundButton title='Attack' cardID={props.cardID} sound='A' />
      <View style={{padding: 4}} />
      <SoundButton title='Death' cardID={props.cardID} sound='D' />
    </View>
  )
}

export default MinionDefaultSounds
