import React, { useState, useEffect } from 'react';
import MinionDefaultSounds from './MinionDefaultSounds';
import HeroDefaultSounds from './HeroDefaultSounds';
import { Text, View, Image, ScrollView } from 'react-native';
import heroes from './assets/heroes.json';

const Card = (props) => {

  const heroIDs = heroes.map(a => a.id)
  const isHero = heroIDs.includes(props.cardID)
  const imgSrc = (isHero ?
    'https://hearthstonesounds.s3.amazonaws.com/' :
    'https://art.hearthstonejson.com/v1/render/latest/enUS/256x/')
     + props.cardID + '.png'

  return (
    <ScrollView>
      <View style={{ flex: 1, padding: 8, alignItems: 'center'}}>
        {isHero
          ? <Image source={{uri: imgSrc}} style={{width: 250, height: 345}}/>
          : <Image source={{uri: imgSrc}} style={{width: 256, height: 388}}/>
        }
        <Text style={{width: 512, paddingBottom: 16}}>{props.flavorText}</Text>
        <View
          style={{
            height: 1,
            width: "100%",
            opacity: 0.25,
            backgroundColor: "#000000",
            marginRight: 8,
            marginBottom: 16
          }}
        />
        {isHero
          ? <HeroDefaultSounds cardID={props.cardID}/>
          : <MinionDefaultSounds cardID={props.cardID}/>
        }
      </View>
    </ScrollView>
  )
}

export default Card
