import React from 'react';
import CardListTab from './CardListTab';
import SetList from './SetList';
import { NavigationContainer } from '@react-navigation/native';
import { createBottomTabNavigator } from '@react-navigation/bottom-tabs';
import { NavigationEvents } from "react-navigation";

const App = ({navigation}) => {
  const Tab = createBottomTabNavigator();

  return (
    <NavigationContainer>
      <Tab.Navigator>
        <Tab.Screen name="Sets" component={SetList}/>
        <Tab.Screen
          name="Cards"
          component={CardListTab}
          initialParams = {{cardSet: null, typeFilter: "MINION"}}
         />
         <Tab.Screen name="Heroes" component={CardListTab} initialParams = {{cardSet: null, typeFilter: "HERO"}} />
       </Tab.Navigator>
    </NavigationContainer>
  )
}

export default App;
